.skills {
    text-align: center;
    padding: 4rem;

}

.skill {
    margin-bottom: 20px;
}

.status-bar {
    background-color: #f0f0f0;
    height: 10px;
    width: 200px;
    border-radius: 10px;
    overflow: hidden;
}

.status {
    height: 100%;
    width: 60%;
    background-color: #1ab13e;
    /* Adjust color as needed */
}

.skill-heading {
    font-weight: bold;
    font-size: 3.5rem;
    position: relative;
}

.skill-name {
    color: white;
    display: block;
    margin-bottom: 5px;
    font-weight: bold;

}

.skill-heading::before {

    content: 'Skills';
    /* Text for the shadow */
    color: rgba(242, 231, 231, 0.3);
    /* Shadow text color */
    font-size: 5.5rem;
    position: absolute;
    top: 40%;
    left: 43%;
    z-index: -1;
    /* Place behind the main text */
    transform: translate(-10%, -10%);
    /* Adjust position of the shadow text */
    font-family: "Cinzel", serif;

}

.skill-heading::after {

    content: '';
    /* Create a pseudo-element for the shadow effect */
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    /* Shadow color */
    z-index: -2;
    /* Place behind the main text and shadow text */
    transform: translate(-50%, -50%);
    border-radius: 20px;
    /* Adjust border radius as needed */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    /* Adjust shadow properties as needed */
}

.skills-set {
    padding: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

@media screen and (max-width: 600px) {
    .skills {
        padding: 2rem;
    }

    .skill-heading {
        font-weight: bold;
        font-size: 1.8rem;
        position: relative;
    }

    .skills-set {
        margin-top: 5rem;
        background-color: #222222;
        padding: 3rem 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
    }

    .skill-heading::before {

        content: 'Skills';
        /* Text for the shadow */
        color: rgba(242, 231, 231, 0.3);
        /* Shadow text color */
        font-size: 3.5rem;
        position: absolute;
        top: 40%;
        left: 30%;
        z-index: -1;
        /* Place behind the main text */
        transform: translate(-10%, -10%);
        /* Adjust position of the shadow text */
        font-family: "Cinzel", serif;

    }

}