.projects {
    text-align: center;
    padding: 5rem;
}

.sec-1 {
    display: flex;
    margin-top: 4rem;
    padding: 2rem;
    gap: 0rem;
}

.gpuzzle {
    width: 95%;
    height: auto;
    flex: 1;
    border: 1px solid white;

}

.portfolio {
    width: 95%;
    height: auto;
    flex: 2;
    border: 1px solid white;

}

.elearning {
    width: 93%;
    height: 390px;
    border: 2px solid white;
}

.project-heading {
    font-weight: bold;
    font-size: 3.5rem;
    position: relative;
}

.project-heading::before {

    content: 'Projects';
    /* Text for the shadow */
    color: rgba(242, 231, 231, 0.3);
    /* Shadow text color */
    font-size: 5.5rem;
    position: absolute;
    top: 40%;
    left: 37%;
    z-index: -1;
    /* Place behind the main text */
    transform: translate(-10%, -10%);
    /* Adjust position of the shadow text */
    font-family: "Cinzel", serif;

}

.project-heading::after {

    content: '';
    /* Create a pseudo-element for the shadow effect */
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    /* Shadow color */
    z-index: -2;
    /* Place behind the main text and shadow text */
    transform: translate(-50%, -50%);
    border-radius: 20px;
    /* Adjust border radius as needed */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    /* Adjust shadow properties as needed */
}

.project-image-container {
    position: relative;

    /* Set the width of the image container */
    height: auto;
}

.project-image-container:hover .image-overlay {
    opacity: 1;
}

.project-image-container:hover .gpuzzle,
.project-image-container:hover .portfolio {
    filter: grayscale(100%) opacity(0.5);
    /* Add grayscale and opacity on hover */
}

.image-overlay {
    position: absolute;
    top: 0rem;
    left: 1.5rem;
    width: 97%;
    height: 100%;
    background-color: rgba(3, 4, 3, 0.5);
    /* Transparent green background */
    opacity: 0;
    /* Initially transparent, will become opaque on hover */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

p {
    color: white;
    font-weight: bold;
}

.image-overlay h2 {
    margin: 0;
    color: white;
    font-size: 2rem;
    font-weight: bold;
}

.buttons {
    margin-top: 1rem;
}

.buttons button {
    margin: 0 0.5rem;
    padding: 15px 15px;
    background-color: #1AB13E;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}



@media screen and (max-width:600px) {
    .project-heading {
        font-weight: bold;
        font-size: 1.8rem;
        position: relative;
        margin: 0 auto;
        text-align: center;
    }

    .project-heading::before {

        content: 'Projects';
        /* Text for the shadow */
        color: rgba(242, 231, 231, 0.3);
        /* Shadow text color */
        font-size: 3rem;
        position: absolute;
        top: 35%;
        left: 22%;
        z-index: -1;
        /* Place behind the main text */
        transform: translate(-10%, -10%);
        /* Adjust position of the shadow text */
        font-family: "Cinzel", serif;

    }

    .gpuzzle,
    .portfolio {

        width: 100%;
        height: auto;
        border: 1px solid white;
    }

    .sec-1 {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 4rem;
        padding: 0rem;
        gap: 2rem;
    }


    .image-overlay {
        width: 100%;
        height: auto;
        top: 1.5rem;
        left: 0.5rem;
    }

    .image-overlay h2 {
        font-size: 1.5rem;
    }

    .project-detials {
        display: none;
    }

    .elearning {
        width: 100%;
        height: auto;
        border: 1px solid white;
        margin: 2rem 0rem;

    }

    .projects {
        padding: 0rem 3rem 3rem 3rem;
    }
}