.contact {
    text-align: center;
    padding: 5rem;
}

.contact-icons {
    padding: 5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem;
}

.icons {
    color: #1ab13e;
    font-size: 3rem;
    background-color: #222222;
    padding: 2rem;
    border-radius: 50%;
}

.icon-info {
    color: white
}

.contact-heading {
    font-weight: bold;
    font-size: 3.5rem;
    position: relative;
}

.contact-heading::before {

    content: 'Contact';
    /* Text for the shadow */
    color: rgba(242, 231, 231, 0.3);
    /* Shadow text color */
    font-size: 5.5rem;
    position: absolute;
    top: 29%;
    left: 37%;
    z-index: -1;
    /* Place behind the main text */
    transform: translate(-10%, -10%);
    /* Adjust position of the shadow text */
    font-family: "Cinzel", serif;

}

.contact-heading::after {

    content: '';
    /* Create a pseudo-element for the shadow effect */
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    /* Shadow color */
    z-index: -2;
    /* Place behind the main text and shadow text */
    transform: translate(-50%, -50%);
    border-radius: 20px;
    /* Adjust border radius as needed */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    /* Adjust shadow properties as needed */
}



@media screen and (max-width:600px) {
    .contact-heading {
        font-weight: bold;
        font-size: 1.8rem;
        position: relative;
        margin: 0 auto;
        text-align: center;
    }

    .contact-heading::before {

        content: 'Contact';
        /* Text for the shadow */
        color: rgba(242, 231, 231, 0.3);
        /* Shadow text color */
        font-size: 3rem;
        position: absolute;
        top: 35%;
        left: 15%;
        z-index: -1;
        /* Place behind the main text */
        transform: translate(-10%, -10%);
        /* Adjust position of the shadow text */
        font-family: "Cinzel", serif;

    }

    .contact-icons {
        display: flex;
        flex-direction: column;
    }
}