li {
    list-style-type: none;
}

h1 {
    margin: 0;
    color: white;
}

.navbar {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 10px 25px;
    color: white;
    position: relative;
}

.navbar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.fa-icons {
    display: none;
    cursor: pointer;
}

.nav-items {
    display: flex;
    align-items: center;
    gap: 2.5rem;
    margin: 1rem;
    padding: 0;
}

.nav-item {
    display: block;
    font-weight: bold;

}

.user-name {
    padding: 10px;
    border: 2px solid #80d39b;
    border-radius: 5px;
    color: #80d39b;
}



@media screen and (max-width: 600px) {
    .fa-icons {
        display: block;
    }

    .nav-items {
        display: none;
        flex-direction: column;
        gap: 1rem;
        position: absolute;
        top: 60px;
        left: 0;
        background-color: #000;
        width: 100%;
        padding: 10px;
    }

    .nav-items.open {
        display: flex;
    }

    .nav-items {
        align-items: flex-start;
    }
}