.details {
    padding: 1rem;
}

.project-detail {

    background-color: #1f1f1f;
    /* Dark background color */
    color: #fff;
    /* Light text color */
    padding: 20px;
    border-radius: 10px;
}

.project-name {
    text-align: center;
    font-weight: bold;
    color: #1ab13e;

}

.project-description,
.project-sample-results {
    padding: 2rem;
    margin-bottom: 20px;
}

.project-title {
    color: #1ab13e;
    font-size: 24px;
    margin-bottom: 2rem;
}

.project-text {
    font-size: 16px;
    line-height: 1.6;
}

.project-result-images {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 2rem;
}

.project-image {
    margin: 0.3rem;
    width: calc(33.333% - 10px);
    /* Adjust image width based on the number of images */
    border-radius: 5px;
}

/* You can add more styles as needed */