.achievements {
    text-align: center;
    padding: 5rem;
}

.achievements-heading {
    font-weight: bold;
    font-size: 3.5rem;
    position: relative;
}

.certifications {
    display: flex;
    padding: 3rem;
    align-items: center;
    justify-content: space-evenly;
    margin: 4rem;
}

.certfication-info {
    width: 40%;
    height: auto;
    padding: 40px 30px;
    border-radius: 8px;
    background-color: #222222;
    text-align: left;
    color: white;
}

.certification-name {
    color: #1ab13e;
}



.view-btn {
    padding: 10px 20px;
    color: white;
    background-color: #1ab13e;
    border: none;
    border-radius: 3px;
}

.achievements-heading::before {

    content: 'Certifications';
    /* Text for the shadow */
    color: rgba(242, 231, 231, 0.3);
    /* Shadow text color */
    font-size: 5.5rem;
    position: absolute;
    top: 25%;
    left: 30%;
    z-index: -1;
    /* Place behind the main text */
    transform: translate(-10%, -10%);
    /* Adjust position of the shadow text */
    font-family: "Cinzel", serif;

}

.achievements-heading::after {

    content: '';
    /* Create a pseudo-element for the shadow effect */
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    /* Shadow color */
    z-index: -2;
    /* Place behind the main text and shadow text */
    transform: translate(-50%, -50%);
    border-radius: 20px;
    /* Adjust border radius as needed */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    /* Adjust shadow properties as needed */
}

@media screen and (max-width:600px) {
    .achievements {
        text-align: center;
        padding: 5rem;
    }

    .achievements-heading {
        font-weight: bold;
        font-size: 1.8rem;
        position: relative;
        margin: 0 auto;
        text-align: center;
    }

    .achievements-heading::before {

        content: 'Certifications';
        /* Text for the shadow */
        color: rgba(242, 231, 231, 0.3);
        /* Shadow text color */
        font-size: 2.7rem;
        position: absolute;
        top: 35%;
        left: 0%;
        z-index: -1;
        /* Place behind the main text */
        transform: translate(-10%, -10%);
        /* Adjust position of the shadow text */
        font-family: "Cinzel", serif;

    }

    .certifications {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        align-items: center;
        justify-content: space-evenly;
        padding: 0;

    }

    .certfication-info {
        width: 16rem;

    }

    .certification-name {
        font-size: 1.5rem;
        text-align: left;
    }

    .certification-year {
        font-size: 1.3rem;
        text-align: left;
        margin: 1rem 0;
    }


}