.home {
    color: white;
    display: flex;
    align-items: center;
    gap: 4rem;
    padding: 20px;
}

h1 {
    color: white;
}

/* .user-img {
    width: 20%;
    height: auto;
} */


.social-icons a {
    color: #80d39b;
}

.heading {
    color: #80d39b;
    ;
}

.smart-phone {
    background-color: white;
    border-radius: 20px;
    /* Adjust the border-radius to make it look like a smartphone */
    box-shadow: 0px 0px 10px rgba(241, 238, 238, 0.1);
    /* Add shadow to create depth */
    padding: 0px 20px 20px 20px;
    /* Add padding for spacing */
    display: flex;
    flex-direction: column;
    /* Arrange children in a column */
    align-items: center;
    gap: 2rem;
    /* Center children horizontally */
    width: 80%;
    /* Set width to 80% of the parent container */
    max-width: 270px;
    /* Set maximum width to prevent it from getting too wide */
    height: auto;
    /* Allow height to adjust based on content */
    margin-top: 2rem;
    border: 4px solid #1AB13E;
}


.heading {
    margin: 0;
    /* Remove default margin */
    color: #1AB13E
        /* Set text color to black */
}

.cam {
    color: black;
    margin: 0;
}

.user-img {
    width: 80%;
    /* Adjust image width */
    border-radius: 10px;
    /* Apply border-radius to image */
    margin-top: 20px;
    /* Add margin for spacing */
}

.social-icons {
    display: flex;
    margin-top: 20px;
    gap: 1rem;

    /* Add margin for spacing */
}

.social-icons a {
    margin: 4rem 10px 0rem 10px;
    /* Add margin between icons */
    color: #1AB13E
        /* Set icon color to black */
}

.user-info {
    flex: 2;
}

.text1,
.text2,
.text3 {
    margin: 2rem 0rem;
}

.text1,
.text3 {
    color: #1AB13E
}

@media screen and (max-width: 600px) {
    .home {
        padding: 5rem 0rem;
        display: flex;
        flex-direction: column;
        padding: 0;
        margin-top: 14rem;

    }



    .smart-phone {
        width: 60%;
        margin: 1rem;
    }

    .heading {
        font-size: 1.6rem;
    }

    .user-info {
        width: 100%;
        padding: 0;
        margin: 0 auto;
        display: none;
    }
}