.about-me {
    text-align: center;
    display: flex;
    align-items: center;
    padding-top: 6.5rem;

}

.name {
    color: #1AB13E
}

.content {
    color: white;
}

.my-info {
    display: flex;
    flex-direction: column;
}

.name {

    /* Adjust as needed */
    display: inline-block;
    text-align: right;
    /* Align text to the right */
}

.my-info-detials {
    margin: 2rem 0rem;
    font-size: 1.3rem;
    text-align: left;
}

.content {
    display: inline-block;

    /* Adjust as needed */
    margin-left: 10px;
    /* Adjust as needed */

    /* Align text to the left */
}

.my-img {
    width: 58%;
    height: auto;
}

.about-heading {
    color: white;
    font-weight: bold;
    font-size: 3.5rem;
    margin: 2rem;
    position: relative;
    /* Ensure positioning context */
}

.about-heading::before {
    content: 'About';
    /* Text for the shadow */
    color: rgba(242, 231, 231, 0.3);
    /* Shadow text color */
    font-size: 5.5rem;
    position: absolute;
    top: 1.5rem;
    left: 1.8rem;
    z-index: -1;
    /* Place behind the main text */
    transform: translate(-10%, -10%);
    /* Adjust position of the shadow text */
    font-family: "Cinzel", serif;

}

.about-heading::after {
    content: '';
    /* Create a pseudo-element for the shadow effect */
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    /* Shadow color */
    z-index: -2;
    /* Place behind the main text and shadow text */
    transform: translate(-50%, -50%);
    border-radius: 20px;
    /* Adjust border radius as needed */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    /* Adjust shadow properties as needed */
}

.resume-btn {
    font-size: 1rem;
    font-weight: bold;
    background-color: #1AB13E;
    color: white;
    width: 50%;
    padding: 15px 10px;
    border: none;
    border-radius: 25px;
    margin: 0 auto;
}




@media screen and (max-width: 600px) {
    .about-me {
        font-weight: bold;
        font-size: 1.8rem;
        position: relative;
        margin-bottom: 3rem;
        text-align: center;
    }

    .about-heading {
        font-weight: bold;
        text-align: center;
        font-size: 1.8rem;
        margin: 1rem 2rem 0rem 0;

    }

    .about-heading::before {

        content: 'About';
        /* Text for the shadow */
        color: rgba(242, 231, 231, 0.3);
        /* Shadow text color */
        font-size: 3.5rem;
        position: absolute;
        top: 0.7rem;
        left: 6.3rem;
        z-index: -1;
        /* Place behind the main text */
        transform: translate(-10%, -10%);
        /* Adjust position of the shadow text */
        font-family: "Cinzel", serif;
    }

    .my-info-detials {

        padding: 4rem 1.5rem 1.5rem 1.5rem;
        font-size: 1rem;
        margin: 0 0 0 3.5rem;
    }

    .my-img {
        display: none;
    }

    .resume-btn {
        font-size: 0.9rem;
    }
}